/* Signup.css */

/* Body styles */
body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    background-color: #ffffff; /* White background */
  }
  
  /* Container styles */
  .signup-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #ffffff; /* White background */
  }
  
  /* Form styles */
  .signup-form {
    background-color: #28a745; /* Green theme color */
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    text-align: center;
  }
  
  .signup-form h2 {
    color: #ffffff; /* White text color */
    margin-bottom: 20px;
  }
  
  /* Form input styles */
  .form-group {
    margin-bottom: 15px;
  }
  
  .form-group label {
    display: block;
    font-weight: bold;
    margin-bottom: 5px;
    color: #ffffff; /* White text color */
  }
  
  .form-group input {
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 4px;
  }
  
  /* Error message styles */
  .error-message {
    color: #dc3545; /* Red text color */
    margin-top: 10px;
  }
  
  /* Signup button styles */
  .signup-button {
    background-color: #ffffff; /* White button background color */
    color: #28a745; /* Green button text color */
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    cursor: pointer;
    font-weight: bold;
    transition: background-color 0.3s ease;
  }
  
  .signup-button:hover {
    background-color: #d4edda; /* Light green on hover */
  }
  